import React, { useMemo, useState, useEffect, useCallback } from 'react';
import { Table, Popover, Whisper, Checkbox, Dropdown, IconButton, Progress, Pagination } from 'rsuite';
import MoreIcon from '@rsuite/icons/legacy/More';
import "./index.css";
import TableComponent from '../../../components/RsuiteTable/index.jsx';
import moment from 'moment';
import { Link } from 'react-router-dom';
import { useTheme } from '@mui/material/styles';
import OutlinedInput from '@mui/material/OutlinedInput';
import MenuItem from '@mui/material/MenuItem';
import FormControl from '@mui/material/FormControl';
import Select from '@mui/material/Select';
import DatePickerElement from '../../../components/DateRangePicker';
import Axios from '../../../api/Axios';
import Loader from '../../../components/Loader';
import { useNavigate } from 'react-router-dom';
import triggerSwalAlert from '../../../helpers/triggerSwalAlert';
import { CSVLink } from "react-csv";


const axios = new Axios();

const ProductOrders = () => {
  const navigate = useNavigate();
  const { Column, HeaderCell, Cell } = Table;
  const [checkedKeys, setCheckedKeys] = useState([]);
  const [data, setData] = useState([]);
  const [action, setAction] = useState("");
  const [selectedOrderStatus, setSelectedOrderStatus] = useState("ALL");
  const [copyData, setCopyData] = useState([]);
  const [loading, setLoading] = useState(false);
  const [sortColumn, setSortColumn] = useState();
  const [sortType, setSortType] = useState();
  const [limit, setLimit] = useState(10);
  const [page, setPage] = useState(1);
  const [csvData, setCSVData] = useState([]);
  const [originalOrdersData, setOriginalOrdersData] = useState([]);
  const [totalSales, setTotalSales] = useState(0);
  const [totalUnitsSold, setTotalUnitsSold] = useState(0);
  // const [searchKey, setSearchKey] = useState('');
  const orderStatusFilters = [
      {
          "id": "all",
          "value": "ALL"
      },
      {
          "id": "processing",
          "value": "PROCESSING"
      },
      {
        "id": "onhold",
        "value": "ON HOLD"
      },
      {
          "id": "completed",
          "value": "COMPLETED"
      },
      {
          "id": "cancelled",
          "value": "CANCELLED"
      },
      {
          "id": "refunded",
          "value": "REFUNDED"
      },
      {
          "id": "failed",
          "value": "FAILED"
      }
  ]
  const [pending, setPending] = useState(false)
  const [finalDate, setFinalDate] = useState([new Date(), new Date()])


  let checked = false;
  let indeterminate = false;

  const ITEM_HEIGHT = 48;
  const ITEM_PADDING_TOP = 8;
  const MenuProps = {
    PaperProps: {
      style: {
        maxHeight: ITEM_HEIGHT * 4.5 + ITEM_PADDING_TOP,
        width: 250,
      },
    },
  };

  if (checkedKeys.length === data.length) {
    checked = true;
  } else if (checkedKeys.length === 0) {
    checked = false;
  } else if (checkedKeys.length > 0 && checkedKeys.length < data.length) {
    indeterminate = true;
  }

  const handleCheckAll = (value, checked) => {
    const keys = checked ? data.map(item => item.id) : [];
    setCheckedKeys(keys);
  };
  const handleCheck = (value, checked) => {
    const keys = checked ? [...checkedKeys, value] : checkedKeys.filter(item => item !== value);
    setCheckedKeys(keys);
  };

  const CheckCell = ({ rowData, onChange, checkedKeys, dataKey, ...props }) => (
    <Cell {...props} style={{ padding: 0 }}>
      <div style={{ lineHeight: '46px' }}>
        <Checkbox
          value={rowData[dataKey]}
          inline
          onChange={onChange}
          checked={checkedKeys.some(item => item === rowData[dataKey])}
        />
      </div>
    </Cell>
  );

  const StatusCell = ({ rowData, dataKey, ...props }) => (
    <Cell {...props} style={{ padding: 0 }}>
      <div
        style={{
          background: `${rowData.status == 'COMPLETED' ? '#00ff00' : rowData.status == 'PROCESSING' ? '#FFA500' : rowData.status == 'CANCELLED' ? '#e0e0e0' : 'transparent'}`,
          borderRadius: 6,
          padding: "5px 10px",
          marginTop: "8px",
          overflow: 'hidden',
          display: 'inline-block'
        }}
      >
        <div>{rowData.status}</div>
      </div>
    </Cell>
  );

  const handleData = useCallback(async () => {
    setPending(true)
    const options = {
      method: "GET"
    }
    const result = await axios.callApi(`order/getOrders?start=${moment(finalDate[0]).startOf("day").valueOf()}&end=${moment(finalDate[1]).endOf("day").valueOf()}&type=${(selectedOrderStatus != "ALL") ? selectedOrderStatus : ""}`, options)
    if (result.status == 200) {
      setPending(false)
      let ordersArr = []
      let totalSales = 0
      let totalUnitsSold = 0
      result?.data?.data.forEach((order) => {
        ordersArr.push({
          id: order.id,
          orderNo: `${order.orderToken}`,
          shiprocketOrderNo: `${order.id}`,
          name: `${order.addressDetails.shippingAddress.firstName} ${order.addressDetails.shippingAddress.lastName}`,
          date: moment(order.placedAt).format("YYYY-MM-DD h:m a"),
          status: order.orderStatus,
          city: order.addressDetails.shippingAddress.city,
          purchaseNo: order.purchaseCount,
          total: parseInt(order.orderTotalAmount),
        })
        if(["CANCELLED", "REFUNDED", "FAILED"].includes(order.orderStatus)){
          return
        }
        totalSales += parseInt(order.orderTotalAmount)
        order.orderDetails.forEach(product => {
           totalUnitsSold += parseInt(product.count)
        })
      })
      setTotalSales(totalSales)
      setTotalUnitsSold(totalUnitsSold)
      setData(ordersArr);
      setCopyData(ordersArr);
      setOriginalOrdersData(result?.data?.data)
    }
    else {
      setPending(false)
    }
  }, [finalDate, selectedOrderStatus])

  // const setFiltersArray = (orders)=>{
  //   let filtersValuesCount = [{all: 0}, {processing: 0}, {completed: 0}, {cancelled: 0}, {refunded: 0}, {failed: 0}]

  //   orders.forEach(order=>{
  //     filtersValuesCount[0].all += 1
  //     switch(order.orderStatus){
  //       case "PROCESSING":
  //         filtersValuesCount[1].processing += 1
  //         break 
  //       case "COMPLETED":
  //         filtersValuesCount[2].completed += 1
  //         break 
  //       case "CANCELLED":
  //         filtersValuesCount[3].cancelled += 1
  //         break 
  //       case "REFUNDED":
  //         filtersValuesCount[4].refunded += 1
  //         break 
  //       case "FAILED":
  //         filtersValuesCount[5].failed += 1
  //         break
  //       default:
  //         break
  //     }
  //   })
  //   let filtersArr = filtersValuesCount.map(item => {
  //     return {
  //       "id": Object.keys(item)[0],
  //       "value": `${Object.keys(item)[0].toUpperCase()} (${Object.values(item)[0]})`
  //     }
  //   })
  //   setOrderStatusFilters(filtersArr)
  //   return filtersArr
  // }

  useEffect(() => {
    handleData()
  }, [finalDate, selectedOrderStatus])

  const handleBulkStatusChange = async (event) => {
    if(checkedKeys.length == 0) return
    const { target: { value } } = event;
    setPending(true)
    setAction(value);
    let options = {
        method: "POST",
        body: {
            status: value,
            ids: checkedKeys
        }
    }
    let result = await axios.callApi("/order/bulkUpdateOrderStatus", options);
    if(result.status == 200){
        setPending(false)
        let temp = JSON.parse(JSON.stringify(copyData))
        temp.map(item=> {
          if(checkedKeys.includes(item.id)){
            item.status = value
          }
        })
        setData(temp);
        setCopyData(temp);
        triggerSwalAlert("Success", "Status Updated Successfully", "success")
    }
    else{
        setPending(false)
        triggerSwalAlert("Error", "Error while updating the status of order", "error")
    }
  };

  const handleFilterChange = (event) => {
    const {target: { value }} = event;
    setSelectedOrderStatus(value)
  //   let selectedFilter = selectedOrderStatus.split(' ')[0];
  //  let filteredData = copyData.filter((item)=> item.status == selectedFilter)
  //  if(searchKey.length !== 0 && selectedFilter.toLowerCase() !== 'all'){
  //   let newFilteredData = filteredData.filter(
  //       (e) =>
  //         e?.name
  //           ?.toLocaleLowerCase()
  //           .includes(searchKey.trim().toLocaleLowerCase())
  //     );
  //     setData(newFilteredData)
  //     setBackup(filteredData)
  //  }else{
  //   setData(filteredData)
  //   setBackup(filteredData)
  //  }
  //  if(selectedFilter.toLowerCase() == 'all'){
  //      if(searchKey.length !== 0){
  //   let newFilteredData = copyData.filter(
  //       (e) =>
  //         e?.name
  //           ?.toLocaleLowerCase()
  //           .includes(searchKey.trim().toLocaleLowerCase())
  //     );
  //     setData(newFilteredData)
  //     setBackup(newFilteredData)
  //  }else{
  //   setData(copyData)
  //   setBackup(copyData)
  //  }
  //  }
  };

  const actions = [
    {
      id: 2,
      value: "PROCESSING",
      label: 'Change status to processing'
    },
    {
      id: 3,
      value: "ON HOLD",
      label: 'Change status to on-hold'
    },
    {
      id: 4,
      value: "COMPLETED",
      label: 'Change status to completed'
    },
    {
      id: 5,
      value: "CANCELLED",
      label: 'Change status to cancelled'
    }
  ];

  const onSearch = useCallback((key) => {
    // setSearchKey(key)
    const temp = copyData.filter(
      (e) =>
        e?.name
          ?.toLocaleLowerCase()
          .includes(key.trim().toLocaleLowerCase())
    );
    setData(temp);
  }, [copyData]);

  const getData = () => {
    if (sortColumn && sortType) {
      return data.sort((a, b) => {
        let x = a[sortColumn];
        let y = b[sortColumn];
        if (typeof x === 'string') {
          x = x.charCodeAt();
        }
        if (typeof y === 'string') {
          y = y.charCodeAt();
        }
        if (sortType === 'asc') {
          return x - y;
        } else {
          return y - x;
        }
      });
    }
    return data;
  };

  const handleSortColumn = (sortColumn, sortType) => {
    setLoading(true);
    setTimeout(() => {
      setLoading(false);
      setSortColumn(sortColumn);
      setSortType(sortType);
    }, 500);
  };

  const handleChangeLimit = dataKey => {
    setPage(1);
    setLimit(dataKey);
  };

  const tableData = getData().filter((v, i) => {
    const start = limit * (page - 1);
    const end = start + limit;
    return i >= start && i < end;
  });

  const onChangeDateRange = (value) => {
    setFinalDate(value)
  }

  //   let windowWidth = document.documentElement.clientWidth;
  //   let w = (20 * windowWidth ) / 100;
  //   let w = (20 * 16 ) / 100;

  const headers = [
    "PARTNER",
    "DATE",
    "EAN CODE",
    "PRODUCT NAME",
    "QTY",
    "MRP SALES",
    "NET(DISCOUNTED SALES)",
    "LOCATION"
  ]

  const downloadFile = () => {
    let dataVal = []
    originalOrdersData.forEach((order) => {
      if(["CANCELLED", "REFUNDED", "FAILED"].includes(order.orderStatus)){
        return
      }
      order.orderDetails.forEach(product => {
         dataVal.push({
          "PARTNER": "WEBSITE",
          "DATE": moment(order.placedAt).format("DD/MM/YYYY"),
          "EAN CODE": product.refNumber,
          "PRODUCT NAME": product.productName + " " + Object.values(product.attributeCombination),
          "QTY": product.count,
          "MRP SALES": product.productPrice * product.count,
          "NET(DISCOUNTED SALES)": product.productDiscountPrice * product.count,
          "LOCATION": order.addressDetails.shippingAddress.city
         })
      })
    })
    setCSVData(dataVal)
  }


  return (
    <>
      {pending ? <Loader /> : null}
      <div className="container ordersContainer">
        <div className="header my-4">Orders</div>
        <div className='d-flex align-items-center justify-content-between'>
          <FormControl sx={{ m: 1, width: 300, mt: 3 }}>
            <Select
              displayEmpty
              value={action}
              onChange={handleBulkStatusChange}
              input={<OutlinedInput />}
              renderValue={(selected) => {
                if (!selected) {
                  return <em>Bulk actions</em>;
                }
                return selected;
              }}
              MenuProps={MenuProps}
              inputProps={{ 'aria-label': 'Without label' }}
            >
              {actions.map((item) => (
                <MenuItem
                  key={item.id}
                  value={item.value}
                >
                  {item.label}
                </MenuItem>
              ))}
            </Select>
          </FormControl>
          <div className='d-flex align-items-center' style={{gap:"0 0.75rem"}}>
            <DatePickerElement onChangeDateRange={onChangeDateRange} />
            <FormControl sx={{ width: 300, mt: 3 }}>
              <Select
                displayEmpty
                value={selectedOrderStatus}
                onChange={handleFilterChange}
                input={<OutlinedInput />}
                renderValue={(selected) => {
                  if (!selected) {
                    return <em>Filters</em>;
                  }
                  return selected;
                }}
                MenuProps={MenuProps}
                inputProps={{ 'aria-label': 'Without label' }}
              >
                {orderStatusFilters.map((item) => (
                  <MenuItem
                    key={item.id}
                    value={item.value}
                  >
                    {item.value}
                  </MenuItem>
                ))}
              </Select>
            </FormControl>
            <form className={`nosubmit`} onSubmit={(e) => { e.preventDefault(); }}>
              <input className="nosubmit" type="search" placeholder="Search" onChange={(e) => onSearch(e.target.value)} />
            </form>
            <CSVLink
            data={csvData}
            filename="orders.csv"
            headers={headers}
            onClick={downloadFile}
            asyncOnClick={true}
            style={{
              display: "inline",
              // height: "fit-content",
              // marginRight: "8px",
              marginTop: "0px",
              textDecoration:"none"
            }}
            >
            <button className="btn ordersDownload">Download</button>
            </CSVLink>
          </div>
        </div>
        <Table className='my-4'
          height={300}
          id="table"
          bordered
          loading={loading}
          data={tableData}
          sortColumn={sortColumn}
          sortType={sortType}
          onSortColumn={handleSortColumn}>
          <Column width={100} align="center">
            <HeaderCell style={{ padding: 0 }}>
              <div style={{ lineHeight: '40px' }}>
                <Checkbox
                  inline
                  checked={checked}
                  indeterminate={indeterminate}
                  onChange={handleCheckAll}
                />
              </div>
            </HeaderCell>
            <CheckCell dataKey="id" checkedKeys={checkedKeys} onChange={handleCheck} />
          </Column>
          <Column align="center" sortable resizable>
            <HeaderCell>Order No.</HeaderCell>
            <Cell dataKey="orderNo" />
          </Column>
          <Column align="center" sortable resizable>
            <HeaderCell>Shiprocket Order No.</HeaderCell>
            <Cell dataKey="shiprocketOrderNo" />
          </Column>
          <Column align="center" sortable resizable>
            <HeaderCell>Name</HeaderCell>
            <Cell dataKey="name" />
          </Column>
          <Column align="center" resizable>
            <HeaderCell></HeaderCell>
            <Cell dataKey="id">
              {rowData =>
                <svg onClick={()=>navigate(`/view-order/${rowData.id}`)} xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="currentColor" className="bi bi-eye-fill" viewBox="0 0 16 16">
                  <path d="M10.5 8a2.5 2.5 0 1 1-5 0 2.5 2.5 0 0 1 5 0z" />
                  <path d="M0 8s3-5.5 8-5.5S16 8 16 8s-3 5.5-8 5.5S0 8 0 8zm8 3.5a3.5 3.5 0 1 0 0-7 3.5 3.5 0 0 0 0 7z" />
                </svg>}
            </Cell>
          </Column>
          <Column width={100} sortable resizable>
            <HeaderCell>Purchase No.</HeaderCell>
            <Cell dataKey="purchaseNo" />
          </Column>
          <Column width={100} sortable resizable>
            <HeaderCell>City</HeaderCell>
            <Cell dataKey="city" />
          </Column>
          <Column width={300} sortable resizable>
            <HeaderCell>Date</HeaderCell>
            <Cell dataKey="date" />
          </Column>
          <Column width={235} sortable resizable>
            <HeaderCell>Status</HeaderCell>
            <StatusCell style={{ padding: '10px 0' }} dataKey="status">
              {/* {rowData => <Progress percent={rowData.progress} showInfo={false} />} */}
            </StatusCell>
          </Column>
          <Column width={300} sortable resizable>
            <HeaderCell>Total</HeaderCell>
            <Cell style={{ padding: '10px 0' }} dataKey="total">
              {rowData => <div>&#8377; {rowData.total}</div>}
            </Cell>
          </Column>
        </Table>
        <div style={{ padding: 20 }}>
          <Pagination
            prev
            next
            first
            last
            ellipsis
            boundaryLinks
            maxButtons={5}
            size="xs"
            layout={['total', '-', 'limit', '|', 'pager', 'skip']}
            total={getData().length}
            limitOptions={[10, 30, 50]}
            limit={limit}
            activePage={page}
            onChangePage={setPage}
            onChangeLimit={handleChangeLimit}
          />
        </div>
        <div style={{ textAlign: 'right' }}>
          <span>
              Total Sales - &#8377;{totalSales}
          </span>
          <span style={{ marginLeft: '10px' }}>
              Total Units Sold - {totalUnitsSold}
          </span>
        </div>
      </div>
    </>
  )
}

export default ProductOrders;