import React, { useRef, useEffect } from 'react';
import DateRangePicker from 'react-bootstrap-daterangepicker';
import './index.css';
import { DatePicker } from './Style';
import 'bootstrap-daterangepicker/daterangepicker.css';

const DatePickerElement = (props) => {
  const { onChangeDateRange } = props
  const todaySampleDate = [new Date().setHours(0, 0, 0, 0), new Date().setHours(0, 0, 0, 0)]
  const [startDate, endDate] = [new Date().setHours(0, 0, 0, 0), new Date().setHours(0, 0, 0, 0)]
  const pickerRef = useRef()

  useEffect(() => {
    pickerRef.current.setStartDate(startDate ?? todaySampleDate[0])
    pickerRef.current.setEndDate(endDate ?? todaySampleDate[1])

  }, [startDate, endDate])

  const applyDateRangeHandler = (event) => {
    let selectedDateRangeArr = event.target.value.split(" - ")
    selectedDateRangeArr = selectedDateRangeArr.map(date => new Date(date))
    onChangeDateRange && onChangeDateRange(selectedDateRangeArr)
  }

  return (
    <form className="dateRange">
      <DateRangePicker
        ref={pickerRef} initialSettings={{ locale: { format: 'MMM DD YYYY' }, showDropdowns: true, opens: 'left' }}
        onApply={applyDateRangeHandler}>
        <DatePicker readOnly={true} />
      </DateRangePicker>

    </form>
  )

}

export default DatePickerElement;