import { useState } from 'react';
import { Table, Pagination,SelectPicker } from 'rsuite';
import 'rsuite/dist/rsuite.min.css';
const { Column, ColumnGroup, HeaderCell, Cell } = Table;

const TableComponent = (columns,data) => {
  const [page, setPage] = useState(1);
  const [limit, setLimit] = useState(10);
  const limitOptions = [10, 50, 100];
  const columnData = columns.columns;

  const handleChangeLimit = (dataKey) => {
    setPage(1);
    setLimit(dataKey);
  };

//   const paginationData = data?.filter((v, i) => {
//     const start = limit * (page - 1);
//     const end = start + limit;
//     return i >= start && i < end;
//   });


  return (
    <div className='datarstable'>
      <Table className='rstable' id="dataTable" fillHeight={true} bordered cellBordered headerHeight={60} data={data}>
        {columnData?.map((item,index) => {
        return ( 
          <>
          { !item?.children ? (
            <>
              <Column key={index} width={item.width} align="center" resizable>
              <HeaderCell key={index} style={{background:"#fff",textTransform:"none"}}>{item?.displayName}
              { item?.headerElement && item?.headerElement}
              </HeaderCell>
              {item?.cell 
              ? 
              (<Cell key={index} style={index == "0" ? {textTransform:"none",background:"#F1F3F5"} : {textTransform:"none"}} dataKey={item?.accessor}>
              {item.cell}
              </Cell>)
              : 
              (<Cell key={index} style={{textTransform:"none"}} dataKey={item?.accessor} />)}
              </Column>
            </>
          ):(
            <>
              <ColumnGroup key={index} align="center" header={item?.displayName} style={{background:"#fff"}}  >
                {item.children.map((child)=>{
                  return (
                    <Column key={index} width={child.width} align={item.align ? item.align : "center"} resizable >
                  <HeaderCell key={index} style={{background:"#fff"}}>{child?.childName}</HeaderCell>
                  {child?.cell
                  ? 
                  <Cell key={index} dataKey={child?.accessor}>{child?.cell}</Cell>
                  : 
                  <Cell key={index}  dataKey={child?.accessor} />
                }
                </Column>
                  )
                })}
                
              </ColumnGroup>
            </>
          )}

          </>
        )
        })} 
      </Table>
      <div style={{ padding: 15, display:"flex",alignItems:"center",justifyContent:"center" }}>
          <Pagination
            prev
            next
            first
            last
            ellipsis
            boundaryLinks
            maxButtons={5}
            size="md"
            total={data.length}
            limit={limit}
            limitOptions={limitOptions}
            activePage={page}
            onChangePage={setPage}
            onChangeLimit={handleChangeLimit}
            />
            <span style={{ marginLeft: 20, fontSize:"12px" }}>
            Results per page
            <SelectPicker
              value={limit}
              onChange={handleChangeLimit}
              cleanable={false}
              searchable={false}
              placement="topStart"
              style={{marginLeft:"10px",border: "1px solid #E3E7EB",color:"#4A4E52"}}
              data={limitOptions.map((key) => ({ value: key, label: key }))}
            />
            </span>
      </div>
    </div>

  );
};

export default TableComponent;
